@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
html {
	scroll-behavior: smooth;
}

body {
	height: 100%;
	background: linear-gradient(to right, #485563, #29323c);
}
.main-page {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;

    font-family: 'Roboto Slab', serif;
}

.main-item {
    /* width: 70%; */
    /* align-items: left; */
    /* margin-bottom: 20px;
    margin-top: 20px; */
}
.header-component {
    position: absolute;
    width: 100%;
    background-color: #ffd460;
    background-image: url(/alanoli-dev/static/media/keyboard_2.7a2e30dd.png);
    background-position: center;
    /* background-size: cover; */
    background-size: 400px 200px;
    /* background-position; */
    overflow: hidden;
    font-family: 'Roboto Slab', serif;
}

.header-component .header-title {
    color: #2d4059;
    font-weight: bold;
    margin: 0;
    /* text-decoration: underline;
    text-decoration-color: #ffd460; */
}

.ant-page-header {
    /* background: none;
    background-color: transparent; */
    z-index: 10;
    padding: 5px 24px;
}

.header-icon {
    height: 30px;
    width: 30px;
}

.header-icon:focus {
    outline: none;
}
.subheader {
    width: 100%;
    font-family: 'Roboto Slab', serif;
    background-color: #2d4059;
    height: 100vh;

    overflow: hidden;
}

.subheader .sub-root {
    padding-top: 54px; /* Header */
    height: 100%;
    max-height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.subheader .sub-root .title {
    display: flex;
    align-items: center;
    align-self: center;
    flex-direction: column;
    justify-content: center;
}

.subheader .sub-root .title p {
    font-size: 7vw;
    color: white;
    width: 80%;
    text-align: center;
    margin: 0;
    padding: 15px 0;
}

.subheader .sub-root .projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: space-evenly; */
    
    height: 100%;
    max-height: 100%;
    max-width: 100%;
    width: 100%;
}

.subheader .sub-root .projects .reference {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    text-align: center;
    margin: auto;
    width: 100%;

    padding: 0 0 0 25px;
}

.subheader .sub-root .projects .reference .projects-descr {
    font-size: 1.2rem;
    margin: 0;
}

.subheader .sub-root .projects .reference .more {
    font-size: 0.8rem;
    margin: 0;
    border-bottom: 2px solid #ffd460;
}

.subheader .sub-root .projects .reference button {
    background-color: #ffd460;
    color: #2d4059;
    font-weight: bolder;
    padding: 7px 15px;
    border-radius: 5px;
    border: none;
}

.subheader .sub-root .projects .reference button:hover {
    background-color: white;
    color: #2f89fc;
}

.subheader .sub-root .projects .reference .link-root .link {
    color: white;
    font-size: 0.8rem;
    margin: 0;
}

.subheader .sub-root .projects .project-content  {
    height: 100%;
    width: 100%;
}

.subheader .sub-root .projects .project-content {
    height: 90%;
    width: 90%;

    display: flex;
    flex-direction: row;
    overflow-x: auto;
}

.subheader .sub-root .projects .project-content::-webkit-scrollbar {
    display: none;
}

.subheader .sub-root .projects .project-content img {
    width: 80%;
    /* height: 50%; */
    /* max-height: 100%; */
    object-fit: contain;
    margin-right: 20px;
}

.subheader .sub-root .nav-below {
    margin-bottom: 5px;
}

.subheader .sub-root .nav-below span {
    color: white;
    width: 1rem;
    height: 1rem;
}

@media only screen and (min-width: 768px) {

    .subheader .sub-root {
        /* background-image: none;
        background-color: grey; */
    }

    .subheader .sub-root .title {
        width: 75%;
        /* height: 50px; */
    }

    .subheader .sub-root .title p {
        font-size: 40px;
    }

    .subheader .sub-root .projects .project-content {
        height: 90%;
        width: 75%;
        /* max-width: 800px; */
    
        display: flex;
        flex-direction: row;
        /* justify-content: center; */
        overflow-x: auto;
    }

    .subheader .sub-root .projects .reference {
        width: 75%;
    }

    /* .subheader .sub-root .projects .project-content img {
        display: inline-block;
    } */

    .subheader .sub-root .projects .project-content img {
        width: 50%;
        /* height: 100%; */
        object-fit: contain;
    }
}
.contact-page {
	width: 100%;
	background-color: #2d4059;
	font-family: 'Roboto', sans-serif;
	padding-bottom: 40px;
}

.contact-page .contact-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 80%;
	margin: auto;

	background-color: #324e7b;

	/* border: 1px solid rgb(218, 218, 218); */
	/* box-shadow: 1px 1px 10px rgb(216, 214, 214); */
	border-radius: 7px;
	padding: 20px 0;
}

.contact-page .contact-container .btn {
	background-color: #ffd460;
	color: #324e7b;
	font-weight: bold;
}

.contact-page .contact-container .title {
	width: 100%;
	color: #ffd460;
	font-weight: bold;
	text-align: center;
}

.contact-page .contact-container form div {
	width: 100%;
}

@media only screen and (min-width: 768px) {

    .contact-page .contact-container {
        max-width: 600px;
    }
}
.footer-root {
    border-top: 1px solid #666;
    background: black;
    color: white;

    position: relative;
    width: 100%;
    bottom: 0;
    height: 20rem;
    font-family: 'Roboto', sans-serif;

    display: flex;
    flex-direction: column;

    padding-top: 40px;
}

.footer-root .text {
    color: rgb(167, 167, 167);
}

.footer-root .main-title {
    flex: 1 1;

    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    text-align: left;

    font-size: 1.7rem;
    padding-left: 30px;
}

.footer-root .middle {
    flex: 3 1;

    padding-top: 20px;
    display: grid;
    grid-template-columns: 1fr 2fr;
    padding-left: 30px;
}

.footer-root .links {
    display: flex;
    flex-direction: column;
    font-size: 1rem;
}

.footer-root .links .link {
    margin-right: auto;
    font-size: 1rem;
}

.footer-root .links .link:hover {
    cursor: pointer;
    color: white;
    transition-duration: 0.6s;
}

.footer-root .footer-footer {
    flex: 1 1;

    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}
.portifolio-content-root {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 100%;
    padding-top: 20px;
    border-bottom: 1px dashed gray;
    font-family: 'Roboto Slab', serif;
}

.portifolio-content-root img {
    height: 100px;
}

.portifolio-content-root .meta {
    display: flex;
    flex-direction: column;
    align-items: left;

    width: 90%;
}

.portifolio-content-root .meta h2, p {
    color: white;
    text-align: left;
}

.portifolio-content-root .meta h2 {
    font-size: 1.5rem;
}

.portifolio-content-root .meta .descr {
    width: 100%;
}

.portifolio-content-root .tech {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: left;
    margin-bottom: 30px;
}

.portifolio-content-root .tech span {
    background-color: #ffd460;
    border-radius: 7px;
    color: #2d4059;
    font-weight: bold;
    margin-right: 10px;
    margin-bottom: 5px;
    padding: 5px;
}

@media only screen and (min-width: 768px) {

    .portifolio-content-root {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    
        width: 60%;
        padding-top: 20px;
        border-bottom: 1px dashed gray;
    }

    .portifolio-content-root img {
        height: 300px;
        margin-right: 50px;
        margin-bottom: 30px;
    }

    .portifolio-content-root .meta .descr {
        width: 80%;
    }
}
